			position: absolute;
			@include vcenter;
			width: 65px;
			height: 6px;
			background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAEEAAAAGCAYAAACcqvORAAAAXElEQVQ4T+3UwQmAQAxE0bcWYAN24tkiLETsxRbswYuCtiULgqlhd3MKuf1PZpJ/Oix4cIV78Wv6CAfsGDHhKJ48AGYJMzb0NYFH1iYBLQ5BQv6OXIwrbpw1ReMFHpUKBZoLEZ0AAAAASUVORK5CYII=) no-repeat center;
			@include md {
				top: 30%; }
			@include sm {
				background-size: contain;
				width: 30px; }
			@include xs {
				top: -10%; } } }
	.slick-next {
		right: -10px;
		&:before {
			content: '';
			display: block;
			position: absolute;
			@include vcenter;
			width: 65px;
			height: 6px;
			background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAEEAAAAGCAYAAACcqvORAAAAUElEQVQ4T+3UwQmAMBQE0ZdOtCc78BQbEOzRFsQiPAqRD6aKn+1gBmaLfJuw4cAb+CWfAzMunFhwh4SWUERHfrAOCdSRw59DthriGCv2Dv4B5IANud7hb+wAAAAASUVORK5CYII=) no-repeat center;
			@include md {
				top: 30%; }
			@include sm {
				background-size: contain;
				width: 30px; }
			@include xs {
				top: -10%; } } }
	.slick-dots {
		display: flex;
		justify-content: center;
		position: absolute;
		left: 0;
		right: 0;
		bottom: -30px;
		z-index: 10;
		li {
			width: 14px;
			height: 14px;
			background: #eee;
			border-radius: 50%;
			overflow: hidden;
			margin: 0 11px;
			cursor: pointer;
