			font-size: 13px;
			letter-spacing: 1px; } }
	&__body-wrp {
		position: relative;
		padding: 0 0 97px;
		@include sm {
			padding: 0 0 50px; }
		@include xs {
			padding: 0 0 30px; } }
	&__body {
		margin: 50px 0 0;
		position: relative;
		@include xs {
			margin: 25px 0 0; }
		&.is-short {
			height: 110px;
			overflow: hidden;
			&:after {
				content: '';
				display: block;
				position: absolute;
				left: 0;
				right: 0;
				bottom: 0;
				z-index: 1;
				height: 90px;
				background: linear-gradient(to top, #ffffff 0%, rgba(255, 255, 255, 0) 100%); } }
		.seo__title {
			&:first-of-type {
				margin: 0; } } }
	&__title {
		@include bold;
		font-size: 18px;
		line-height: 1.2;
		color: $black;
		margin: 30px 0 0;
		@include xs {
			font-size: 14px;
			margin: 15px 0 0; } }
	&__txt {
		margin: 15px 0 0;
		font-size: 13px;
		@include regular;
		line-height: 1.4;
		@include xs {
			margin: 7px 0 0; } }
	&__open {
		@extend %more-button;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translateX(-50%);
		cursor: pointer; } }
