.about-bg {
	background-color: $bg_gray; }
.promo,
.advantages,
.about-order {
	@include cover;
	position: relative; }
// promo
.promo {
	min-height: 210px;
	@include xs {
		min-height: 180px; }
	&__title {
		@include vcenter;
		@include bold;
		font-size: 36px;
		letter-spacing: 7.2px;
		color: $white;
		text-transform: uppercase;
		@include sm {
			font-size: 26px; } } }
// about
.about {
	&__in {
		padding-top: 60px;
		padding-bottom: 86px;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		@include md {
			flex-direction: column;
			padding-top: 40px;
			padding-bottom: 40px;
			max-width: 480px; }
		@include xs {
			padding: 25px 15px;
			text-align: center; } }
	&__col {
		flex: 1;
		&:last-of-type {
			margin-left: 27px;
			@include md {
				margin: 20px 0 0 0; }
			@include xs {
				margin: 15px 0 0 0;
				line-height: 23px; } } }
	&__title,
	&__subtitle {
		color: #181818; }
	&__title {
		margin-bottom: 18px;
		font-size: 42px;
		line-height: 42px;
		@include md {
			font-size: 33px;
			line-height: 33px; }
		@include xs {
			font-size: 24px;
			line-height: 24px; }
		span {
			text-transform: uppercase; } }
	&__subtitle {
		font-size: 18px;
		line-height	24px {}
		@include bold;
		@include xs {
			font-size: 16px;
			line-height: 22px; } }

	&__text {
		color: $gray;
		line-height: 30px;
		font-size: 14px;
		@include xs {
			line-height: 23px; } } }
// advantages
.advantages {
	min-height: 340px;
	color: $white;
	&__in {
		padding-top: 39px;
		padding-bottom: 48px;
		max-width: 863px;
		@include xs {
			padding: 25px 15px; } }
	&__title {
		color: $white; }
	&__list {
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 14px;
		margin-top: 35px;
		flex-wrap: wrap;
		@include xs {
			margin-top: 20px;
			flex-direction: column; } }
	&__item {
		border: 2px solid #ffffff;
		border-radius: 50%;
		width: 200px;
		height: 200px;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
		text-align: center;
		padding: 47px 28px;
		@include md {
			margin: 17px;
			padding: 30px 20px; }
		@include sm {
			margin: 13px;
			padding: 20px; }
		h4 {
			text-transform: uppercase;
			@include bold;
			letter-spacing: 2.8px;
			margin-bottom: 15px;
			@include xs {
				margin: 10px; } }
		p {
			line-height: 18px;
			letter-spacing: 0.35px; }
		&:not(:first-child) {
			margin-left: 107px;
			@include md {
				margin: 17px; }
			@include sm {
				margin: 13px; } } } }
// team
.team {
	&__in {
		padding-top: 38px;
		padding-bottom: 58px;
		display: flex;
		flex-wrap: wrap;
		justify-content: center;
		@include md {
			flex-direction: column;
			max-width: 480px;
			padding-bottom: 40px; }
		@include xs {
			padding: 25px 15px; } }
	&__title {
		margin-bottom: 33px;
		flex: 1 100%;
		@include xs {
			margin-bottom: 20px; } }
	&__pic {
		@include xs {
			max-width: 320px;
			margin: auto; } }
	&__content {
		text-align: right;
		font-size: 14px;
		color: $gray; }
	&__text {
		line-height: 30px;
		@include xs {
			line-height: 23px; }
		&:not(:first-child) {
			margin-top: 30px;
			@include md {
				margin-top: 20px; }
			@include xs {
				margin-top: 10px; } } }
	&__col {
		flex: 1;
		&:first-of-type {
			@include md {
				order: 2;
				margin-top: 20px; }
			@include xs {
				margin-top: 10px; } }
		&:last-of-type {
			margin-left: 30px;
			@include md {
				order: 1;
				margin-bottom: 0; } } } }
// about-order
.about-order {
	min-height: 387px;
	padding: 77px 0 127px 0;
	@include md {
		padding-bottom: 60px; }
	@include xs {
		padding: 40px 0; }
	&__in {
		max-width: 950px; }
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 387px;
		background-image: linear-gradient(to bottom, #ffffff 12%, rgba(255, 255, 255, 0.66) 42%, rgba(255, 255, 255, 0.33) 71%, rgba(255, 255, 255, 0) 100%); }
	&__form {
		z-index	1 {}
		position: relative; }
	&__cooperation {
		padding-bottom: 70px;
		&:before {
			background-image: linear-gradient(to bottom, #f4f4f4 12%, rgba(244, 244, 244, 0.66) 42%, rgba(255, 255, 255, 0.33) 71%, rgba(255, 255, 255, 0) 100%); }
		.form {
			margin-top: 29px; } } }
