// catalog-promo
.catalog-promo {
	@include cover;
	min-height: 315px;
	text-align: center;
	color: $white;
	position: relative;
	padding: 50px 0;
	@include xs {
		padding: 25px 0;
		min-height: 115px; }
	&:before {
		content: '';
		display: block;
		position: absolute;
		top: 0;
		z-index: 0;
		left: 0;
		background-color: rgba(12, 1, 19, 0.25);
		width: 100%;
		height: 100%; }
	&__in {
		max-width: 607px;
		z-index: 1;
		position: relative;
		font-size: 0; }
	&__title {
		letter-spacing: 1.05px;
		@include bold;
		text-shadow: 0 1px 0 #000000;
		font-size: 42px;
		@include sm {
			font-size: 35px; }
		@include xs {
			font-size: 27px;
			max-width: 244px;
			line-height: 33px;
			margin: auto; } }
	&__subtitle {
		font-size: 14px;
		line-height: 24px;
		margin: 14px 0 40px 0;
		letter-spacing: 0.35px;
		@include xs {
			margin: 23px auto;
			text-align: center;
			line-height: 16px;
			max-width: 300px;
			br {
				display: none; } } }
	&__btn {
		min-width: 240px;
		@include xs {
			min-width: 200px;
			font-size: 12px; } } }


// cards
.cards {
	background-color: $bg_gray;
	&__in {
		padding-top: 35px;
		padding-bottom: 127px;
		flex-direction: column;
		@include md {
			max-width: 760px;
			padding-bottom: 60px; }
		@include sm {
			max-width: 480px; }
		@include sm {
			max-width: 370px;
			padding-top: 25px;
			padding-bottom: 25px; } }
	&__header,
	&__in,
	&__main,
	&__aside,
	&__list {
		display: flex; }
	&__aside {
		flex-shrink: 0;
		flex-grow: 0;
		max-width: 348px;
		width: 35%;
		@include r(1100) {
			max-width: 300px; }
		@include md {
			order: 1;
			max-width: 480px;
			margin: auto;
			width: 100%;
			.aside__body {
				padding-bottom: 0; } }
		// flex: 0 1 348px
		margin-right: 36px; }
	&__header {
		margin: 17px 0 50px;
		position: relative;
		@include md {
			margin-bottom: 25px;
			flex-direction: column;
			.title-primary {
				text-align: center;
				padding: 0;
				span {
					&:before {
						display: none; } } } } }
	&__main {
		// margin-top: 10px
		flex-wrap: wrap;
		flex-grow: 1;
		align-items: flex-start; }
	&__body {
		padding-top: 16px;
		border-top: 1px solid #dedede;
		// width: 66%
		flex: 1;
		width: calc(100% - 384px);
		@include r(1100) {
			width: calc(100% - 336px); }
		@include md {
			order: 3;
			width: 100%; }
		@include sm {
			max-width: 340px;
			margin: auto; } }
	&__list {
		flex-wrap: wrap;
		justify-content: center;
		padding-top: 10px;
		padding-bottom: 7px;
		margin: 0 0 0 -3%;
		@include sm {
			flex-direction: column;
			margin: 0;
			padding-bottom: 29px; } }
	&__item {
		width: 47%;
		max-width: 350px;
		margin-left: 3%;
		flex-grow: 0;
		flex-shrink: 1;
		padding: 12px 0;
		@include sm {
			width: 100%;
			margin: auto; } }
	&__tab-links {
		position: absolute;
		top: -10px;
		right: 200px;
		@include md {
			position: static;
			margin: 30px auto; }
		@include sm {
			margin: 15px auto; } } }
.cards-sort {
	display: flex;
	flex: 1 1 100%;
	justify-content: space-between;
	align-items: flex-start;
	margin-bottom: 10px;
	@include md {
		order: 2;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		margin: 36px auto 33px; }
	&__l {
		flex: auto;
		@include md {
			order: 2;
			width: 100%; } }
	&__r {
		width: calc(100% - 384px);
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include r(1100) {
			width: calc(100% - 336px); }
		@include md {
			width: 100%;
			order: 1;
			flex-direction: column;
			.search {
				display: none; } } }
	&__title {
		color: $gray2;
		font-size: 14px;
		letter-spacing: 0.35px;
		margin-bottom: 3px; } }
.cards__list-col3 {
	.cards__item {
		width: 30.33%;
		@include md {
			width: 45.33%; }
		@include sm {
			width: 100%; } } }
.cards__list-tab {
	display: none;
	&.is-active {
		display: flex; } }
.card {
	&__top {
		position: relative; }
	&__slider {
		height: 221px;
		&:before {
			content: '';
			z-index: 2;
			display: block;
			@include coverdiv;
			background-color: rgba(12, 1, 19, 0.15); }
		.slick-list.draggable {
			overflow: hidden; }
		.slick-arrow {
			width: 65px;
			position: absolute;
			top: 48%;
			transform: translateY(-50%);
			padding: 20px;
			cursor: pointer;
			z-index: 10;
			transition: opacity 0.25s;
			@include hide-text;
			&:hover {
				opacity: 0.4; } }
		.slick-prev {
			left: 0%;
			&:before {
				content: '';
				display: block;
				position: absolute;
				@include vcenter;
				width: 14px;
				height: 21px;
				background: url(data:image/png;base64,) no-repeat center; } }
		.slick-next {
			right: 0%;
			&:before {
				content: '';
				display: block;
				position: absolute;
				@include vcenter;
				width: 14px;
				height: 21px;
				background: url(data:image/png;base64,) no-repeat center; } }
		.slick-dots {
			display: flex;
			justify-content: center;
			position: absolute;
			left: 0;
			right: 0;
			bottom: 10px;
			z-index: 10;
			li {
				width: 9px;
				height: 9px;
				background: rgba($white, 0.5);
				border-radius: 50%;
				overflow: hidden;
				margin: 0 5px;
				cursor: pointer;
				transition: background 0.25s;
				&:hover,
				&.slick-active {
					background: $white; } }
			button {
				@include hide-text; } } }
	&__link {
		@include coverdiv;
		z-index: 4; }
	&__content {
		padding: 10px 20px 20px 16px;
		background-color: $white;
		@include xs {
			padding: 6px 26px 13px 20px; } }
	&__title {
		font-size: 24px;
		@include bold;
		@include xs {
			font-size: 21px; } }
	&__l {
		color: $gray;
		margin: 10px 0 23px 0;
		font-size: 18px;
		@include xs {
			font-size: 14px;
			line-height: 24px; } }
	&__footer {
		display: flex;
		align-items: center;
		justify-content: space-between;
		border-top: 1px solid #e9e9e9;
		padding-top: 19px; }
	&__btn {
		min-width: 180px;
		font-size: 10px;
		@include xs {
			min-width: 160px; } } }
.card__slider-item {
	position: relative;
	overflow: hidden;
	width: 350px;
	height: 221px;
	img {
		@include coverdiv;
		object-fit: cover; } }


.price-wrp {
	box-shadow: 0 12px 24px rgba(28, 28, 28, 0.15);
	background-color: $black;
	display: flex;
	min-width: 210px;
	align-items: center;
	height: 49px;
	position: absolute;
	padding: 13px 20px;
	top: 16%;
	left: 26%;
	transform: translate(-50%, -50%);
	z-index: 3; }
.price {
	line-height: 39.44px;
	text-transform: uppercase;
	@include bold;
	&__bl {
		color: $black;
		font-size: 18px;
		@include xs {
			font-size: 14px; } }
	&__wh {
		font-size: 30px;
		color: $white; } }



// objects
.objects {
	display: flex;
	font-size: 14px;
	text-transform: uppercase;
	justify-content: center;
	color: $black;
	@include regular;
	flex-wrap: wrap;
	max-width: 485px;
	padding-top: 10px;
	@include md {
		font-size: 18px; }
	&__title {
		@include md {
			flex: 1 1 100%;
			text-align: center; }
		letter-spacing: 0.7px; }
	&__col {
		flex: 1;
		display: flex;
		flex-wrap: wrap;
		@include md {
			margin: 20px auto;
			justify-content: center; } }
	&__link {
		font-size: 11px;
		letter-spacing: 0.5px;
		padding: 3px 5px;
		color: $black;
		cursor: pointer;
		@include tr(color .25s ease);
		@include sm {
			@include bold;
			padding: 8px 15px; }
		&:not(:first-child) {
			&:hover,
			&.is-active {
				color: $red; } } }
	&__current {
		color: $gray2; } }


// aside
.aside {
	flex-direction: column;
	&__col {
		margin-bottom: 20px; }
	&__title {
		font-size: 18px;
		@include bold;
		text-transform: uppercase;
		letter-spacing: 0.9px;
		padding-bottom: 14px;
		border-bottom: 1px solid $gray2;
		margin-bottom: 25px; }
	&__top {
		margin-bottom: 23px;
		position: relative; }
	&__body {
		padding: 20px;
		background-color: $white;
		box-shadow: 0 10px 49px rgba(28, 28, 28, 0.15);
		@include xs {
			padding: 20px 12px; } }
	&__col {
		margin-bottom: 20px; }
	&__content {
		padding-right: 36px;
		margin-bottom: 40px;
		position: relative;
		@include r(1100) {
			padding-right: 0; }
		@include md {
			padding-right: 68px; }
		@include xs {
			margin-bottom: 20px; } }
	&__btns {
		display: flex;
		justify-content: space-between;
		align-items: center;
		padding-left: 10px;
		@include md {
			justify-content: center;
			padding-left: 0;
			.btn-close {
				margin-left: 20px; } }
		@include sm {
			.btn-close {
				margin-left: 0;
				position: absolute;
				right: 25px; } }
 }		// margin-right: -30px
	&__toggle {
		display: none;
		font-size: 14px;
		background-color: $bg_gray;
		letter-spacing: 0.35px;
		padding: 23px 15px;
		position: relative;
		cursor: pointer;
		&.is-active {
			&:after {
				@include rotate(180); } }
		&:after {
			content: '';
			display: block;
			position: absolute;
			top: 0;
			width: 0;
			height: 0;
			border-style: solid;
			border-width: 4px 4.5px 0;
			border-color: #000 transparent transparent;
			bottom: 0;
			right: 19px;
			margin: auto;
			@include tr(transform .3s ease-in); }
		@include xs {
			display: flex; } }
	&__map {
		@include xs {
			margin: 0 -12px;
			h5 {
				display: none; } } }
	&__pic {
		@include xs {
			overflow: hidden;
			max-height: 0;
			@include tr(max-height .3s ease-in);
			img {
				width: 100%; } } }
	h5 {
		font-size: 14px;
		color: $gray;
		letter-spacing: 0.35px;
		margin-bottom: 10px; } }

