// placeholders
%logo {
	background: url(../img/logo-red.png) no-repeat center;
	background-size: contain;
	width: 200px;
	height: 50px;
	display: block;
	transition: opacity 0.25s;
	@include hide-text;
	&:hover {
		opacity: 0.7; } }
%header {
	position: fixed;
	z-index: 20;
	top: 0;
	left: 0;
	right: 0;
	background: $white; }
.inner {
	max-width: 1140px;
	margin: 0 auto;
	padding: 0 15px; }
.out {
	flex-grow: 1;
	padding: 128px 0 0;
	@include md {
		padding: 70px 0 0; } }
/* == Header start == */
.header {
	@extend %header;
	@include md {
		display: none; }
	&__top {
		height: 38px;
		display: flex;
		justify-content: space-between;
		position: relative;
		&:after {
			content: '';
			display: block;
			position: absolute;
			background-color: #e5e5e5;
			left: 0;
			right: 0;
			bottom: 0;
			height: 1px; } }
	&__top-left {
		display: flex;
		align-items: center; }
	&__top-rigth {
		display: flex;
		align-items: center; }
	&__bottom {
		height: 90px;
		display: flex;
		justify-content: space-between;
		position: relative; }
	&__bottom-left,
	&__bottom-right {
		display: flex;
		align-items: center; }
	&__bottom-center {
		position: absolute;
		left: 50%;
		top: 50%;
		transform: translate(-50%, -50%); }
	&__btn {
		min-width: 172px;
		font-size: 12px;
		@include bold;
		letter-spacing: 0.6px;
		margin: 0 0 0 56px;
		@include lg {
			margin: 0 0 0 20px; } }
	&__messengers {
		margin: 0 0 0 10px; }
	&__logo {
		@extend %logo; } }
.nav {
	position: relative;
	display: flex;
	align-items: center;
	margin: 0 -15px;
	@include lg {
		margin: 0 -10px; }
	&__link {
		color: $black;
		font-size: 10px;
		@include bold;
		line-height: 1.4;
		letter-spacing: 0.25px;
		text-transform: uppercase;
		transition: color 0.35s;
		padding: 10px 0;
		margin: 0 15px;
		position: relative;
		@include lg {
			margin: 0 10px; }
		&:first-of-type {
			padding-left: 0; }
		&:last-of-type {
			padding-right: 0; }
		&:after {
			content: "";
			position: absolute;
			width: 0;
			bottom: 10px;
			height: 1px;
			background: $red;
			right: 0;
			transition: width .35s cubic-bezier(.37,0,.21,1.02); }
		&:hover,
		&.is-active {
			color: $red;
			&:after {
				left: 0;
				right: auto;
				width: 100%; } } } }
.form-links {
	@include bold;
	color: $black;
	font-size: 12px;
	letter-spacing: 0.6px;
	display: inline-flex;
	align-items: center;
	&__item {
		color: inherit;
		font-family: inherit;
		font-size: inherit;
		line-height: 1.4;
		padding: 5px 2px;
		transition: color 0.25s;
		position: relative;
		&:first-of-type {
			padding-left: 0; }
		&:last-of-type {
			padding-right: 0; }
		&:after {
			content: "";
			position: absolute;
			width: 0;
			bottom: 5px;
			height: 1px;
			background: $red;
			right: 0;
			transition: width .35s cubic-bezier(.37,0,.21,1.02); }
		&:hover,
		&.is-active {
			color: $red;
			&:after {
				left: 0;
				right: auto;
				width: 100%; } } }
	&__name {
		text-transform: uppercase;
		&:after {
			display: none; }
		&:hover {
			color: $black; } } }
.tselect {
	@include bold;
	line-height: 1.2;
	color: #000000;
	font-size: 18px;
	text-transform: uppercase;
	letter-spacing: 0.9px;
	height: 100%;
	display: flex;
	align-items: center;
	&.is-active {
		.tselect__list {
			display: block; } }
	&__current {
		padding: 0 20px 0 0;
		position: relative; }
	&__current-link {
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
		color: inherit;
		transition: color 0.25s;
		&:hover {
			color: $red; } }
	&__arrow {
		width: 15px;
		height: 10px;
		position: absolute;
		top: 0;
		right: 0;
		bottom: 0;
		margin: auto;
		cursor: pointer;
		&:before {
			content: '';
			display: block;
			position: absolute;
			@include vcenter;
			@include arr(9, 4, $black, b); } }
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		background: $white;
		z-index: 10;
		display: none; }
	&__item {
		cursor: pointer;
		transition: color 0.25s;
		&:hover {
			color: $red; } } }
.messengers {
	@include hide-text;
	display: inline-flex;
	align-items: center;
	&__item {
		margin: 0 15px 0 0;
		&:last-of-type {
			margin: 0; } }
	&__links {
		display: inline-block;
		cursor: pointer;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } }
	&__whatsapp {
		width: 25px;
		height: 25px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__viber {
		width: 18px;
		height: 18px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__telegram {
		width: 25px;
		height: 25px;
		background: url(data:image/png;base64,) no-repeat center; } }
.pselect {
	color: $black;
	@include regular;
	font-size: 20px;
	line-height: 1.2;
	letter-spacing: 0.5px;
	margin: 0 20px 0 0;
	position: relative;
	&.is-active {
		.pselect__list {
			display: block; } }
	&__current {
		position: relative;
		padding: 0 20px 0 0;
		cursor: pointer;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include arr(9, 4, $black, b);
			top: 0;
			bottom: 0;
			right: 5px;
			margin: auto; } }
	&__list {
		display: none;
		background: $white;
		position: absolute;
		top: 100%;
		left: 0;
		z-index: 10; }
	&__item {
		text-transform: uppercase;
		white-space: nowrap;
		cursor: pointer; } }
.cselect {
	position: relative;
	margin: 0 20px 0 0;
	&.is-active {
		.cselect__list {
			display: block; }
		.cselect__current {
			&:after {
				@include rotate(-180); } } }
	&__current {
		position: relative;
		padding: 0 20px 0 0;
		cursor: pointer;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include arr(9, 4, $black, b);
			top: 0;
			bottom: 0;
			right: 5px;
			margin: auto; } }
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		display: none;
		padding: 5px 0;
		z-index: 10; }
	&__item {
		padding: 5px 0;
		cursor: pointer; }
	img {
		display: block; } }
/* == Header end == */

/* == Header Tablet/Mobile start == */
.header-mob {
	@extend %header;
	display: none;
	@include md {
		display: block; }
	&.is-drop-menu {
		.header-mob__drop {
			transform: translate(0, 0); } }
	@include md {
		display: block; }
	&__top {
		position: relative;
		z-index: 1;
		background: $white;
		box-shadow: 0 0 16px rgba(24, 21, 26, 0.38); }
	&__drop {
		transition: transform 0.3s ease-in-out;
		transform: translate(0, -200%);
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		background: $white;
		padding: 30px 0;
		box-shadow: 0 3px 19px rgba(0, 0, 0, 0.25);
		max-height: 80vh;
		overflow: auto; }
	&__logo {
		@extend %logo;
		height: 25px;
		width: 100px;
		@include xss {
			width: 80px; } }
	&__top-in {
		display: flex;
		align-items: center;
		height: 70px;
		padding: 0 20px; }
	&__drop-in {
		padding: 0 20px; }
	&__feedback {
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 0 -20px;
		padding: 10px 20px; }
	&__nav {
		display: flex;
		flex-direction: column; }
	&__nav-link {
		padding: 10px 0;
		text-transform: uppercase;
		font-size: 14px;
		transition: color 0.25s;
		letter-spacing: 0.1em;
		@include bold;
		&:not(:first-of-type) {
			margin: 6px 0 0; }
		&:hover,
		&.is-active {
			color: $red; } }
	&__btn {
		letter-spacing: 0.07em;
		min-width: 100px; }
	.messengers {
		&__item {
			margin: 0 22px 0 0;
			&:last-of-type {
				margin: 0; } } } }
.form-buttons {
	position: relative;
	display: flex;
	margin: 36px -20px 0;
	&__item {
		width: 50%;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		background: #000;
		position: relative;
		color: $white;
		font-weight: 400;
		font-size: 14px;
		@include bold;
		letter-spacing: 1.4px;
		transition: background 0.25s;
		&:hover,
		&:focus {
			background: lighten(#000, 15%); }
		&_red {
			background: $red;
			&:hover,
			&:focus {
				background: lighten($red, 3%); } } } }

.btn-burger {
	@include hide-text;
	margin: auto -10px auto auto;
	width: 48px;
	height: 36px;
	position: relative;
	display: flex;
	align-items: center;
	transition: transform 0.3s;
	background: none;
	cursor: pointer;
	padding: 10px;
	&.is-active {
		@include rotate(90); }
	&:before,
	&:after {
		content: '';
		display: block;
		position: absolute;
		top: 10px;
		left: 10px;
		height: 2px;
		width: calc(100% - 20px);
		background: $red; }
	&:after {
		top: auto;
		bottom: 10px; }
	span {
		display: block;
		position: absolute;
		height: 2px;
		width: calc(100% - 20px);
		background: $red; } }

.tselect-mob {
	@include bold;
	line-height: 1.2;
	color: #000000;
	font-size: 16px;
	text-transform: uppercase;
	letter-spacing: 0.9px;
	height: 100%;
	display: inline-flex;
	align-items: center;
	margin: 0 0 0 20px;
	position: relative;
	@include xss {
		font-size: 12px; }
	&.is-active {
		.tselect-mob__list {
			display: block; }
		.tselect-mob__current {
			&:after {
				@include rotate(180); } } }
	&__current {
		padding: 10px 20px 0 0;
		position: relative;
		@include xss {
			padding: 5px 20px 0 0; } }
	&__current-link {
		font-family: inherit;
		font-size: inherit;
		line-height: inherit;
		color: inherit;
		transition: color 0.25s;
		&:hover {
			color: $red; } }
	&__arrow {
		width: 15px;
		height: 10px;
		position: absolute;
		top: 14px;
		right: 0;
		cursor: pointer;
		@include xss {
			top: 7px; }
		&:before {
			content: '';
			display: block;
			position: absolute;
			@include vcenter;
			@include arr(9, 4, $black, b); } }
	&__list {
		position: absolute;
		top: 100%;
		margin: -20px 0 0;
		left: 0;
		z-index: 10;
		display: none; }
	&__item {
		cursor: pointer;
		transition: color 0.25s;
		padding: 4px 0 0;
		&:hover {
			color: $red; } } }
.select-mob {
	position: relative;
	margin: 0 -20px;
	+ .select-mob {
		margin: 2px -20px 0; }
	&.is-active {
		.select-mob__list {
			display: block; }
		.select-mob__current {
			&:after {
				@include rotate(180); } } }
	&__current {
		position: relative;
		padding: 0 20px;
		cursor: pointer;
		display: flex;
		align-items: center;
		font-size: 14px;
		@include bold;
		text-transform: uppercase;
		letter-spacing: 0.1em;
		background: #f3f3f3;
		height: 46px;
		&:after {
			content: '';
			display: block;
			position: absolute;
			@include arr(9, 4, $black, b);
			top: 0;
			bottom: 0;
			right: 22px;
			margin: auto;
			transition: transform 0.25s; }
		span {
			display: block;
			margin: 0 0 0 10px; } }
	&__list {
		position: absolute;
		top: 100%;
		left: 0;
		right: 0;
		display: none;
		z-index: 10; }
	&__item {
		cursor: pointer;
		position: relative;
		padding: 0 20px;
		display: flex;
		align-items: center;
		font-size: 14px;
		@include bold;
		text-transform: uppercase;
		letter-spacing: 0.1em;
		background: #f3f3f3;
		transition: background 0.3s;
		height: 46px;
		&:hover,
		&:focus {
			background: darken(#f3f3f3, 3%); } }
	img {
		display: block; } }
/* == Header Tablet/Mobile end == */

/* == Footer start == */
.footer {
	position: relative;
	flex-shrink: 0;
	flex-grow: 0;
	&__top-in {
		position: relative;
		padding: 25px 15px 10px;
		display: flex;
		&:before {
			content: '';
			display: block;
			position: absolute;
			left: 0;
			right: 0;
			top: 0;
			height: 1px;
			background: #e5e5e5; }
		@include xs {
			flex-direction: column;
			align-items: center;
			justify-content: center;
			padding-top: 35px;
			padding-bottom: 45px; } }
	&__top-left {
		width: calc(100% - 300px);
		padding: 5px 0 0;
		@include xs {
			width: 100%; } }
	&__top-right {
		width: 300px;
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		@include xs {
			width: 100%;
			align-items: center;
			margin-top: 35px; } }
	&__bottom {
		background-color: #e8e8e8; }
	&__bottom-in {
		height: 80px;
		display: flex;
		justify-content: space-between;
		align-items: center;
		@include xs {
			height: auto;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			padding-top: 25px;
			padding-bottom: 25px; } }
	&__bottom-left {
		display: flex;
		width: calc(100% - 140px);
		@include xs {
			width: 100%;
			flex-direction: column;
			align-items: center;
			justify-content: center; } }
	&__messengers {
		margin: 15px 0 0;
		@include xs {
			margin: 35px 0 0; } }
	&__logo {
		width: 120px;
		height: 30px;
		background: url(../img/logo-black.png) no-repeat center;
		display: inline-block;
		transition: opacity 0.25s;
		@include hide-text;
		background-size: contain;
		&:hover {
			opacity: 0.7; } } }
.fnav {
	column-count: 3;
	@include sm {
		column-count: 2; }
	@include xs {
		max-width: 342px;
		margin: auto; }
	&__item {
		display: flex; }
	&__link {
		@include regular;
		color: #818181;
		font-size: 16px;
		line-height: 1.4;
		position: relative;
		padding: 5px 20px;
		display: inline-block;
		transition: color 0.25s;
		@include xs {
			font-size: 13px;
			padding: 3px 26px;
			padding-right: 0; }
		&:hover {
			color: darken(#818181, 45%); }
		&:before {
			content: '';
			display: block;
			width: 6px;
			height: 6px;
			border-radius: 50%;
			background: $black;
			position: absolute;
			left: 10px;
			top: 0;
			bottom: 0;
			margin: auto; } } }
.tel {
	color: $black;
	font-size: 30px;
	line-height: 1.2;
	@include bold;
	transition: color 0.25s;
	cursor: pointer;
	&:hover {
		color: lighten($black, 50%); } }
.copyright {
	font-size: 14px;
	line-height: 1.1;
	@include regular;
	color: $black;
	margin: 0 0 0 30px;
	@include xs {
		text-align: center;
		margin: 25px auto;
		line-height: 1.3; }
	@include xss {
		font-size: 11px; } }
.social {
	display: flex;
	&__item {
		margin: 0 15px 0 0;
		display: flex;
		align-items: center;
		&:last-of-type {
			margin: 0; } }
	&__link {
		display: block;
		transition: opacity 0.25s;
		&:hover {
			opacity: 0.6; } }
	&__fb {
		width: 8px;
		height: 17px;
		background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAgAAAARCAYAAADg1u3YAAAAc0lEQVQoU9WQQQ2AMBAEp0pAAg4ICrCABRwgAQeAE3BSCyiALOklLUlJv9yzN93Z1pHOBPRAY8cu2g/A8rpADOxAWwqMwCw4l9ABSnwAFbsAdaiDYgW8AVrm5lTCF3CYQglSVCFqCwpfVNL88T8kr/g/cANTiCGeEtunhQAAAABJRU5ErkJggg==) no-repeat center; }
	&__tw {
		width: 18px;
		height: 15px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__vk {
		width: 26px;
		height: 16px;
		background: url(data:image/png;base64,) no-repeat center; }
	&__insta {
		width: 20px;
		height: 20px;
		background: url(data:image/png;base64,) no-repeat center; } }
/* == Footer end == */
