
.cooperation {
	position: relative;
	@include cover;
	min-height: 500px;
	&:before {
		content: '';
		z-index: 0;
		@include coverdiv;
		background-color: rgba(12, 1, 19, 0.25); }
	&__in {
		position: relative;
		z-index: 1;
		font-size: 0;
		text-align: center;
		padding-top: 61px; }
	&__title {
		font-size: 36px;
		@include bold;
		text-transform: uppercase;
		letter-spacing: 7.2px;
		color: $white;
		@include md {
			font-size: 24px; }
		@include xs {
			font-size:  20px; } }
	&__btns {
		margin-top: 62px;
		@include xs {
			margin-top: 40px; } }
	&__btn {
		@include sm {
			margin: 7px; }
		box-shadow: 0 3px 13px rgba(28, 28, 28, 0.5);
		&:not(:first-child) {
			margin-left: 20px;
			@include sm {
				margin: 7px; } } } }


// audit
.audit {
	&__in {
		max-width: 1050px;
		padding-bottom: 40px;
		@include xs {
			padding-bottom: 25px;
			max-width: 480px; } }
	&__col {
		padding-top: 55px;
		margin-top: -70px;
		@include md {
			margin-top: -124px;
			padding-top: 30px; }
		@include xs {
 } } }			// margin-top: 0
.audit-tabs {
	&__list {
		display: flex;
		font-size: 0;
		justify-content: center;
		position: relative;
		top: -71px;
		flex-wrap: wrap;
		@include md {
			margin: 0 -15px;
			top: -124px;
			max-width: 767px;
			margin: auto; }
		@include xs {
			margin: 0;
			flex-direction: column;
 } }			// top: auto
	&__item {
		display: flex;
		flex: 1;
		align-items: center;
		justify-content: center;
		background: #f4f4f4;
		padding: 10px 24px;
		@include md {
			padding: 0;
			flex: 1 49%;
			margin: 1px;
			height: 60px; }
		@include xs {
			margin: 0; }
		height: 69px;
		position: relative;
		cursor: pointer;
		@include tr(background .25s ease);
		&:before {
			content: '';
			display: block;
			@include coverdiv;
			background: transparent;
			@include tr(transform .25s ease);
			z-index: 1; }
		&:hover {
			background: lighten(#f4f4f4, 3%); }
		&:not(:first-child) {
			margin-left: 2px;
			@include md {
				margin-left: 1px; }
			@include xs {
				margin: 0; } }
		&.is-active {
			background: $white;
			&:before {
				transform: scaleY(1.4);
				background: $white;
				@include md {
					transform: scaleY(1.1); } } } }
	&__title,
	&__ico {
		z-index: 2; }
	&__title {
		font-size: 12px;
		text-transform: uppercase;
		letter-spacing: 0.6px;
		line-height: 18px;
		@include bold;
		max-width: 140px;
		margin-left: 30px;
		@include md {
			margin-left: 15px; } }
	&__ico {
		display: block;
		background-repeat: no-repeat;
		background-position: 50% 50%; } }

// audit-content
.audit-content {
	font-size: 14px;
	line-height: 30px;
	display: none;
	flex-direction: column;
	@include xs {
		line-height: 23px; }
	&.is-active {
		display: flex; }
	&__title {
		line-height: 18px; }
	&__sub {
		margin-top: 15px;
		@include bold;
		text-align: center; }
	&__body {
		margin-top: 38px;
		color: $gray;
		padding-left: 	20px;
		max-width: 945px;
		@include xs {
			margin-top: 25px; }
		h4 {
			color: $black;
			@include bold; } } }
// work
.work {
	background-color: $bg_gray;
	&__in {
		padding-top: 56px;
		max-width: 973px;
		@include md {
			max-width: 480px; }
		@include xs {
			max-width: 320px;
			padding-top: 25px; } }
	&__list {
		margin-top: 58px;
		display: flex;
		flex-wrap: wrap;
		font-size: 0;
		text-align: center;
		@include md {
			margin: 30px -26px 0; }
		@include xs {
			margin: 0;
			margin-top: 15px; } }
	&__item {
		flex: 1;
		@include md {
			padding: 13px; }
		&:not(:first-child) {
			margin-left: 77px;
			@include md {
				margin-left: 0; } } }
	&__l {
		border: 2px solid $red;
		border-radius: 50%;
		width: 160px;
		height: 160px;
		margin: auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center; }
	&__num {
		font-size: 30px;
		color: $black;
		text-transform: uppercase;
		@include bold;
		letter-spacing: 0.75px; }
	&__pic {
		margin-top: 20px; }
	&__desc {
		font-size: 14px;
		max-width: 192px;
		margin: 16px auto 0;
		color: $gray;
		line-height: 18px; } }
