
.personal-area {
	background-color: $bg_gray;
	&__in {
		padding-top: 50px;
		padding-bottom: 100px;
		@include xs {
			padding-bottom: 30px;
			padding-top: 30px; } }
	&__objects {
		margin-top: 58px;
		padding-top: 26px;
		border-top: 1px solid #dedede; } }
// panel
.panel {
	display: flex;
	margin-top: 23px;
	@include sm {
		max-width: 480px;
		flex-direction: column;
		margin: 40px auto 0;
		width: 100%; }
	@include xs {
		margin-top: 20px; }
	&__tabs {
		display: flex;
		flex-direction: column;
		width: 39%;
		font-size: 0;
		justify-content: flex-start;
		flex-grow: 0;
		flex-shrink: 0;
		@include sm {
			width: 100%; } }
	&__content {
		display: none;
		flex-direction: column;
		&.is-active {
			display: flex; } }
	&__main {
		flex-shrink: 1;
		flex-grow: 1;
		z-index: 2;
		position: relative;
		background-color: $white;
		overflow: hidden;
		padding: 17px 15px 35px 25px;
		@include md {
			padding: 17px 15px; }
		@include xs {
			padding: 10px; } }
	&__body {
		padding-left: 43px;
		padding-right: 49px;
		@include md {
			padding-left: 15px;
			padding-right: 15px; }
		@include xs {
			padding-left: 10px;
			padding-right: 10px; } }
	&__header {
		border-bottom: 1px solid rgba(#2c2c2c, 0.15);
		padding-bottom: 14px;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		.user__title {
			text-transform: uppercase;
			@include sm {
				margin-bottom: 0; } } }
	&__btn {
		background: #eaeaea;
		height: 64px;
		display: flex;
		justify-content: flex-start;
		padding: 16px 18px;
		position: relative;
		width: 100%;
		box-shadow: 0;
		&:before {
			content: '';
			z-index: 1;
			display: block;
			@include coverdiv;
			left: 14px;
			top: 3px;
			transform: scale(1.1);
			background: transparent;
			transition: transform .25s ease, background .25s ease, box-shadow .25s ease;
			@include sm {
				@include scale(1);
				top: 0;
				left: 0; } }
		&:not(:first-child) {
			margin-top: 1px; }
		&.is-active,
		&:hover {
			&:before {
				background: lighten($white, 10%);
				box-shadow: -6px 4px 7px rgba(0, 0, 0, 0.05); }
			span {
				color: #fe3e5d; } }

		span {
			color: $black;
			transition: color .25s ease;
			z-index: 2;
			font-size: 12px;
			text-transform: uppercase;
			line-height: 14px; } } }

.panel__header-c {
	justify-content: center; }
.user {
	font-size: 14px;
	line-height: 12px;
	&__in {
		padding: 19px 30px;
		@include md {
			padding: 15px; }
		@include xs {
			padding: 10px; } }
	&__body,
	&__footer {
		margin-top: 15px;
		border: 1px solid rgba(#e0e0e0, 0.5);
		.user__in {
			padding-right: 87px;
			@include md {
				padding-right: 20px; }
			@include xs {
				padding-right: 10px; } }
		.user__title {
			padding-top: 10px; } }
	&__pic {
		overflow: hidden;
		position: relative;
		img {
			border-radius: 50%;
			@include coverdiv;
			object-fit: cover; } }
	&__row {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start;
		@include sm {
			flex-direction: column; }
		&:not(:first-child) {
			margin-top: 16px; } }
	&__col {
		display: flex;
		flex: 0 1 80%;
		&:first-of-type {
			flex: auto; }
		@include sm {
			width: 100%; } }
	&__title {
		color: $gray;
		font-size: 14px;
		margin-right: 12px;
		@include sm {
			margin-right: 0;
			margin-bottom: 5px; } }
	&__info {
		color: $black;
		@include bold; }
	&__field {
		width: 100%; }
	&__fields {
		display: flex; }
	&__input {
		padding: 0 8px;
		@extend %inputs;
		border-radius: 3px;
		height: 32px;
		color: $gray;
		font-size: 12px;
		line-height: 32px;
		border: 1px solid #cccccc; }
	&__btns {
		margin-top: 20px;
		display: flex;
		justify-content: center;
		align-items: center;
		position: relative;
		.btn {
			font-size: 12px;
			margin-right: 20px;
			@include xs {
				margin-right: 10px;
				min-width: 180px; } } }
	&__field {
		&:not(:first-child) {
			margin-top: 18px; } }
	&__btn {
		min-width: 117px;
		display: inline-flex;
		align-items: center;
		text-transform: uppercase;
		justify-content: center;
		border-radius: 0;
		background: #f2f1f1;
		height: 36px;
		line-height: 36px;
		font-size: 12px;
		color: $black;
		&:hover {
			background: lighten(#f2f1f1, 1%); } }
	&__select {
		min-width: 73px;
		margin-right: 8px;
		select {
			background: none;
			border: 1px solid #cccccc; } }
	&__checkboxes {
		margin-top: 20px;
		line-height: 19px;
		display: flex;
		justify-content: flex-start;
		@include sm {
			justify-content: center;
			.checkbox_right .checkbox__field:checked+label:after {
    			right: -.33px;
    			left: auto; } }
		.checkbox {
			&:not(:first-child) {
				margin-left: 40px;
				@include xs {
					margin-left: 15px; } }
			&__title {
				@include bold;
				padding-right: 15px; } } } }
.user__grid-4 {
	width: 35%; }
.user__grid-6 {
	width: 75%;
	font-size: 0; }
.user__grid-4,
.user__grid-6 {
	@include sm {
		width: 100%; } }
.user__grid-12 {
	width: 100%;
	text-align: center;
	font-size: 0;
	position: relative;
	height: 50px;
	.btn {
		margin-left: calc(50% - 80px);
		margin-right: 0;
		@include sm {
			margin-left: 0; }
		// position: absolute
		// top: 50%
		// transform: translateY(-50%)
		// right: 80px
 } }		// margin: 0


.user__col-fl {
	flex-direction: column;
	flex-basis: 73%; }
.user__pic-xss {
	width: 18px;
	height: 18px;
	margin-right: 4px; }
.user__pic-sm {
	width: 36px;
	height: 36px;
	margin-right: 4px; }
.user__pic-lg {
	width: 90px;
	height: 90px; }

.object {
	padding: 20px 10px;
	&__item {
		padding: 0 5px 20px 5px;
		border-bottom: 1px solid rgba(#e0e0e0, .5);
		display: flex;
		align-items: center;
		justify-content: space-between;
		@include sm {
			flex-direction: column; }
		@include xs {
			padding-bottom: 15px; }
		&:not(:first-child) {
			margin-top: 22px; } }
	&__pic {
		width: 120px;
		height: 75px;
		position: relative;
		overflow: hidden;
		@include xs {
			width: 100%;
			max-width: 218px;
			height: 133px; }
		&:before {
			content: '';
			z-index: 1;
			@include coverdiv;
			background-color: rgba(12, 1, 19, 0.15); }
		img {
			@include coverdiv;
			object-fit: cover; } }
	&__description {
		font-size: 12px;
		margin-top: 9px;
		color: $gray;
		line-height: 16px; }
	&__title {
		font-size: 18px;
		@include bold;
		@include xs {
			font-size: 16px;
			margin-top: 5px; } }
	&__btn {
		min-width: 130px;
		font-size: 12px;
		height: 35px; }
	&__catalog {
		width: calc(100% - 130px);
		align-items: flex-start;
		margin-right: 15px;
		@include sm {
			margin-right: 0;
			width: 100%; }
		@include xs {
			flex-direction: column;
			align-items: center;
			justify-content: center; }
		// +sm
		// 	flex-direction: column
		// 	margin-right: 0
		// 	width: 100%
		// 	align-items: center
 }		// 	justify-content: center
	&__catalog,
	&__col {
		display: flex; }
	&__col {
		justify-content: flex-end;
		@include sm {
			margin-top: 5px; } }
	&__content {
		margin-left: 20px;
		line-height: 1;
		@include sm {
			margin-left: 23px; }
		@include xs {
			margin-left: 0;
			order: 1; } } }
.object-maxw {
	max-width: 530px;
	padding-top: 39px;
	@include sm {
		padding-top: 20px;
		max-width: 100%; } }
