.breadcrumb a { letter-spacing: 0.6px; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; font-size: 12px; text-transform: uppercase; color: #000; line-height: 1.2; padding: 5px 2px; display: inline-block; transition: color 0.25s; }

.breadcrumb a:hover { color: #e86278; }

.breadcrumb span { color: #bcbcbc; display: inline-block; padding: 5px 2px; }

.card-main { border: 0; padding: 0; }

.card-main__title { position: relative; margin: 20px 0 0; }

.card-main__title:after { content: ''; display: block; position: absolute; top: 100%; margin: 12px 0 0; left: 0; right: 0; border-top: 1px solid #dedede; z-index: 1; }

.columns { position: relative; display: flex; margin: 30px -15px 0; }

@media only screen and (max-width: 767px) { .columns { flex-direction: column-reverse; } }

.columns__left { width: calc(100% - 285px); padding: 0 15px; flex-grow: 0; flex-shrink: 1; }

@media only screen and (max-width: 767px) { .columns__left { width: 100%; } }

.columns__right { width: 285px; flex-grow: 0; flex-shrink: 1; padding: 0 15px; }

@media only screen and (max-width: 767px) { .columns__right { width: 100%; } }

.columns__title { letter-spacing: 1.8px; text-transform: uppercase; font-size: 18px; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; margin: 0 0 20px; }

.columns__content-wrp { position: relative; padding: 0 0 60px; }

.columns__content { color: #818181; font-size: 16px; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; line-height: 1.2; text-align: left; position: relative; }

.columns__content.is-short { height: 200px; overflow: hidden; }

.columns__content h4 { margin: 30px 0 0; text-transform: uppercase; }

.columns__content p + p { margin: 30px 0 0; }

.columns__content p { font-size: 14px; line-height: 1.6; }

.columns__btn-more { bottom: 0; position: absolute; left: 50%; transform: translateX(-50%); }

.similar { position: relative; padding: 20px 15px; background: #fff; }

.similar__top { letter-spacing: 0.45px; color: #000; font-size: 18px; font-family: "Clear Sans Bold", sans-serif; font-weight: normal; line-height: 1.2; }

.similar__pic { margin: 10px 0 0; position: relative; height: 140px; }

.similar__pic img { width: 100%; height: 100%; object-fit: cover; display: block; }

.similar__txt { color: #818181; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; font-size: 14px; line-height: 1.2; margin: 15px 0 0; }

.similar__btn { width: 100%; min-width: auto; font-size: 12px; font-family: "Clear Sans Bold", sans-serif; font-weight: normal; letter-spacing: 0.6px; display: block; margin: 15px 0 0; }

.liberty-icons { display: flex; margin: 20px -5px 20px; }

.liberty-icons__item { margin: 0 5px; width: 65px; height: 65px; display: flex; align-items: center; justify-content: center; background: #fff; }

.resemling-list-wrp { position: relative; padding: 20px 0 0; margin: 50px 0 0; border-top: 1px solid #dedede; }

.resemling-title { font-family: "Clear Sans Bold", sans-serif; font-weight: normal; color: #000; text-transform: uppercase; letter-spacing: 3.6px; line-height: 1.2; text-align: center; font-size: 18px; }

.resembling-list { display: flex; margin: 20px -15px 0; flex-wrap: wrap; }

.resembling-list__item { padding: 0 15px; width: 50%; flex-grow: 0; flex-shrink: 0; }

@media only screen and (max-width: 767px) { .resembling-list__item { width: 100%; } }

@media only screen and (max-width: 767px) { .resembling-list__item:not(:first-of-type) { margin-top: 24px; } }

.info-object { background: #fff; margin: 0 0 30px; }

.info-object__in { padding: 13px 20px 17px; }

.info-object__row { display: flex; border-bottom: 1px solid #f3f3f3; padding: 7px 0 3px; }

.info-object__row:last-of-type { border: 0; }

.info-object__name { color: #818181; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; font-size: 16px; letter-spacing: 0.4px; line-height: 1.2; width: 70%; flex-grow: 0; }

.info-object__val { font-family: "Clear Sans Bold", sans-serif; font-weight: normal; color: #000; line-height: 1.2; font-size: 16px; letter-spacing: 0.4px; width: 30%; flex-shrink: 0; flex-grow: 0; text-align: right; }

.small-map { position: relative; margin: 40px 0 0; }

@media only screen and (max-width: 599px) { .small-map { margin: 40px -12px 0; } }

.small-map__title { color: #818181; font-size: 14px; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; line-height: 1.2; letter-spacing: 0.35px; padding: 0 0 0 10px; margin: 0 0 5px; }

@media only screen and (max-width: 599px) { .small-map__title { display: none; } }

@media only screen and (max-width: 599px) { .small-map__container { overflow: hidden; max-height: 0; transition: max-height 0.3s ease-in; }
  .small-map__container img { width: 100%; } }

.options { position: relative; }

.options__top { padding: 0 0 15px; border-bottom: 1px solid #dedede; }

.options__title { color: #000; font-family: "Clear Sans Bold", sans-serif; font-weight: normal; line-height: 1.2; font-size: 20px; letter-spacing: 0.35px; }

.options__bottom { border-top: 1px solid #dedede; padding: 30px 0 0; }

.options__btn { width: 100%; display: block; }

.options__bottom-rent { border-top: 0; padding: 20px 0	0; }

.options-list { color: #818181; line-height: 1.2; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; font-size: 14px; letter-spacing: 0.35px; margin: 20px 0; }

.options-list__object { margin: 0; }

.options-list__title { margin: 15px 0 0; }

.options-list__title:first-of-type { margin: 0; }

.options-list__description { font-family: "Clear Sans Bold", sans-serif; font-weight: normal; color: #000; margin: 5px 0 0; }

.rent { font-size: 14px; }

.rent__title { color: #818181; line-height: 1.2; letter-spacing: .35px; font-family: "Clear Sans Regular", sans-serif; font-weight: normal; padding-left: 11px; margin: 20px 0 10px 0; }

.rent__row { display: flex; justify-content: space-between; align-items: center; position: relative; padding: 10px; background-color: #f3f3f3; }

.rent__row .icon-arr { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); font-size: 4px; }

.rent__select { width: 100%; }

.rent__price { display: flex; justify-content: space-between; font-family: "Clear Sans Bold", sans-serif; font-weight: normal; font-size: 18px; margin: 20px 0; }

.rent__info { color: #818181; font-size: 10px; }

.rent__picker { font-size: 14px; color: #000; background: none; }

.rent__date { display: flex; flex: 0 0 30%; max-width: 72px; }
